import React, { Fragment, useEffect, useState } from "react";
import { connect } from "react-redux";
import Table, { TableColumnsInterface } from "../../components/Table/Table";
import { getAllApplicationToAssess } from "../../actions/applicationAssessment";
import { StoreState } from "../../reducers";
import {
  ApplicationAssessmentInterface,
  PeriodInterface,
  PositionInterface,
} from "../../shared/interfaces";
import { fetchPositions } from "../../actions/positionRequests";
import axios from "axios";
import { API } from "../../utils/api";
import { capitalizeFirstLetter } from "../../shared/dataTest";
import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";
import { MdKeyboardArrowRight, MdLabel } from "react-icons/md";
import { GoChecklist } from "react-icons/go";
import { DocumentReducerInterface } from "../../reducers/document";
import {
  getDocumentCategory,
  getDocumentSubCategory,
} from "../../actions/document";
import { IoDocumentOutline } from "react-icons/io5";
import {
  MainItem,
  SideNavigation,
  SubItem,
  SubItemContainer,
} from "../../components/Navigation/SideNavigation";
import UserDocuments from "./UserDocuments";

interface AppProps {
  applicationToAssess: ApplicationAssessmentInterface[];
  getAllApplicationToAssess: Function;
  positions: PositionInterface[];
  fetchPositions: Function;
  documentCategories: DocumentReducerInterface;
  getDocumentCategory: Function;
  getDocumentSubCategory: Function;
}

const Index: React.FC<AppProps> = ({
  applicationToAssess,
  getAllApplicationToAssess,
  fetchPositions,
  positions,
  documentCategories,
  getDocumentCategory,
  getDocumentSubCategory,
}) => {
  const [periodsFilterStatus, setPeriodsFilterStatus] =
    useState<boolean>(false);
  const [thisPeriod, setThisPeriod] = useState<PeriodInterface | null>(null);
  const [positionFilterStatus, setPositionFilterStatus] =
    useState<boolean>(false);
  const [thisPosition, setThisPosition] = useState<PositionInterface | null>(
    null
  );
  const [applicationStatus, setApplicationStatus] = useState<
    "initiated" | "rejected" | "short listed" | ""
  >("");
  const [loadingRequest, setLoadingRequest] = useState<boolean>(false);
  const [application, setApplication] =
    useState<ApplicationAssessmentInterface | null>(null);
  const [periods, setPeriods] = useState<PeriodInterface[]>();
  const [tableCols, setTableCols] = useState<TableColumnsInterface[]>([
    { columnTitle: "#", alignText: "left", padding: "p-3" },
    { columnTitle: "District", alignText: "left", padding: "p-3" },
    { columnTitle: "Names", alignText: "left", padding: "p-3" },
    { columnTitle: "Gender", alignText: "left", padding: "p-3" },
    // { columnTitle: "National ID", alignText: "left", padding: "p-3" },
    { columnTitle: "Phone", alignText: "left", padding: "p-3" },
    // { columnTitle: "Field of Study", alignText: "left", padding: "p-3" },
    { columnTitle: "Post applied for", alignText: "left", padding: "p-3" },
    { columnTitle: "Decision", alignText: "left", padding: "p-3" },
    { columnTitle: "", alignText: "left", padding: "p-3" },
  ]);
  const [modalState, setModalState] = useState<boolean>(false);

  useEffect(() => {
    const loadPeriods = async () => {
      try {
        const res = await axios.get<PeriodInterface[]>(
          `${API.APPLICATION}/applicationsesion`
        );

        setPeriods(res.data);
      } catch (error) {
        console.error({ error: error });
      }
    };

    if (!positions.length) {
      fetchPositions();
    }
    if (!periods || !periods.length) {
      loadPeriods();
    }
    if (!documentCategories.documentCategory.length) {
      getDocumentCategory();
    }
    if (!documentCategories.documentSubCategory.length) {
      getDocumentSubCategory();
    }
  }, [
    documentCategories.documentCategory.length,
    documentCategories.documentSubCategory.length,
    positions.length,
    fetchPositions,
    getDocumentCategory,
    getDocumentSubCategory,
    periods,
  ]);

  useEffect(() => {
    const loadApplications = (
      applicationStatus: string,
      positionId: string,
      periodId: string
    ) => {
      getAllApplicationToAssess(
        applicationStatus,
        positionId,
        periodId,
        setLoadingRequest
      );
    };
    if (thisPeriod && thisPosition && applicationStatus)
      loadApplications(
        applicationStatus,
        thisPosition.position_id,
        thisPeriod.application_session_id
      );
    console.log("load appliactionss: ");
  }, [applicationStatus, getAllApplicationToAssess, thisPeriod, thisPosition]);

  // const getDocFields = (doc_id: string): DocumentAttributesInterface[] => {
  //   let fields, docs;
  //   if (documentCategories.documentCategory) {
  //     docs = documentCategories.documentCategory.filter(
  //       (category) => category.user_detail_category_id === doc_id
  //     );
  //     if (docs.length > 0) {
  //       fields = docs[0].fields;
  //     }
  //   }

  //   if (fields && fields.length) return fields;
  //   return [];
  // };

  const tableData = (): JSX.Element[] => {
    console.log({ applicationToAssess });
    // const data_testing: ApplicationAssessmentInterface[] = [
    //   {
    //     application_id: "6118bf5a23b514ee6ce534db",
    //     user_id: "60edbb68268cae07282d346c",
    //     application_session: "61182d3123b514ee6ce534d7",
    //     application_status: "rejected",
    //     n_id: "1197480080397070",
    //     phone_number: "0722880005",
    //     email: "shyirambere2016@gmail.com",
    //     fname: "Martin",
    //     lname: "MUKWIYE",
    //     sex: "M",
    //     district: "Kayonza",
    //     position: {
    //       courses: ["610252c47d84ba2313cf6148", "610252fd7d84ba2313cf6149"],
    //       position_title: "Teaching mathematics",
    //       description: "Teaching mathematics",
    //       school_level_id: "61005974646ddb0771e8fc52",
    //       education_institute_id: "6100592f646ddb0771e8fc50",
    //       requirements: [
    //         {
    //           user_detail_category_id: "60fc4868f485d8dd91ed321e",
    //           title: "Education",
    //           user_required_docs: [
    //             {
    //               user_detail_subcategory_id: "60fc48dbf485d8dd91ed321f",
    //               title: "A1",
    //             },
    //           ],
    //         },
    //       ],
    //       position_id: "610254047d84ba2313cf614a",
    //     },
    //     details: [],
    //     createdAt: "2021-08-15T07:16:42.436Z",
    //     updatedAt: "2021-08-15T07:16:42.436Z",
    //   },
    // ];
    if (!applicationToAssess.length) {
      return [
        <tr key={1} className="border-b border-gray-200 hover:bg-gray-100">
          <td className="p-3 text-center" colSpan={10}>
            No Data Found
          </td>
        </tr>,
      ];
    }

    return applicationToAssess.map((application, index) => (
      <tr key={index} className="border-b border-gray-200 hover:bg-gray-100">
        <td className="p-3 text-left whitespace-nowrap">{index + 1}</td>
        <td className="p-3 text-left">{application.district}</td>
        <td className="p-3 text-left">
          {application.fname + " " + application.lname}
        </td>
        <td className="p-3 text-left">
          {application.sex === "M" ? "Male" : "Female"}
        </td>
        <td className="p-3 text-left">{application.phone_number}</td>
        <td className="p-3 text-left">{application.position.position_title}</td>
        <td className="p-3 text-left">
          <span
            className={`${
              application.application_status === "rejected"
                ? "bg-red-700"
                : "bg-green-700"
            } text-gray-50 rounded-md py-1 px-2`}
          >
            {capitalizeFirstLetter(application.application_status)}
          </span>
        </td>
        <td className="px-3 text-right">
          <button
            className="bg-blue-600 text-white font-bold py-1 px-1 flex items-center rounded focus:outline-none float-right"
            title="View documents"
            disabled={loadingRequest}
            onClick={() => {
              setApplication(application);
              // setUserDetails(application.details);
              setModalState(true);
            }}
          >
            <IoDocumentOutline className="w-4 h-4" />
            View docs
          </button>
        </td>
      </tr>
    ));
  };

  return (
    <Fragment>
      <SideNavigation>
        {/* <Search data={[1, 2, 3]} /> */}

        {/* <span className="text-sm mt-2">Choose Period</span> */}
        {/* periods sections */}
        <section
          className={`mt-2 ${loadingRequest && "cursor-pointer"}`}
          onClick={() =>
            !loadingRequest && setPeriodsFilterStatus(!periodsFilterStatus)
          }
        >
          <MainItem
            title={
              !thisPeriod
                ? "Choose Period"
                : thisPeriod.application_session_name
            }
            active={periodsFilterStatus || thisPeriod !== null ? true : false}
            // icon={(periodsFilterStatus || thisPeriod !== null) && FaCheckCircle}
          />
        </section>
        {periodsFilterStatus && periods && periods.length > 0 && (
          <SubItemContainer>
            {periods &&
              periods.map((period, index) => (
                <section
                  key={period.application_session_id + index}
                  onClick={() =>
                    !loadingRequest &&
                    (setThisPeriod(period),
                    setPeriodsFilterStatus(false),
                    setThisPosition(null),
                    setApplicationStatus(""))
                  }
                >
                  <SubItem
                    title={period.application_session_name}
                    icon={MdLabel}
                    active={
                      thisPeriod
                        ? period.application_session_id ===
                          thisPeriod.application_session_id
                        : false
                    }
                  />
                </section>
              ))}
          </SubItemContainer>
        )}
        {/* end periods sections */}

        {/* positon sections */}
        {thisPeriod && (
          <section
            className="mt-2"
            onClick={() =>
              !loadingRequest &&
              (setPeriodsFilterStatus(false),
              setPositionFilterStatus(!positionFilterStatus))
            }
          >
            <MainItem
              title={
                !thisPosition ? "Choose Position" : thisPosition?.position_title
              }
              active={
                positionFilterStatus || thisPosition !== null ? true : false
              }
              // icon={(periodsFilterStatus || thisPeriod !== null) && FaCheckCircle}
            />
          </section>
        )}
        {positionFilterStatus && positions && positions.length > 0 && (
          <SubItemContainer>
            {positions &&
              positions.map((position, index) => (
                <section
                  key={position.position_id + index}
                  onClick={() =>
                    !loadingRequest &&
                    (setThisPosition(position),
                    setPositionFilterStatus(false),
                    setApplicationStatus(""))
                  }
                >
                  <SubItem
                    title={position.position_title}
                    icon={MdLabel}
                    active={
                      thisPosition
                        ? position.position_id === thisPosition.position_id
                        : false
                    }
                  />
                </section>
              ))}
          </SubItemContainer>
        )}
        {/* end positon sections */}

        {/* Application Status sections */}
        {thisPosition && (
          <section>
            {!applicationStatus && (
              <span className="text-sm mt-2">Choose Status</span>
            )}
            <section
              className="mt-2"
              onClick={() =>
                !loadingRequest &&
                (setPeriodsFilterStatus(false),
                setPositionFilterStatus(false),
                setApplicationStatus("short listed"))
              }
            >
              <MainItem
                title="Short Listed"
                active={applicationStatus === "short listed"}
              />
            </section>
            <section
              className="mt-2"
              onClick={() =>
                !loadingRequest &&
                (setPeriodsFilterStatus(false),
                setPositionFilterStatus(false),
                setApplicationStatus("rejected"))
              }
            >
              <MainItem
                title="Rejected"
                active={applicationStatus === "rejected"}
              />
            </section>
            <section
              className="mt-2"
              onClick={() =>
                !loadingRequest &&
                (setPeriodsFilterStatus(false),
                setPositionFilterStatus(false),
                setApplicationStatus("initiated"))
              }
            >
              <MainItem
                title="initiated"
                active={applicationStatus === "initiated"}
              />
            </section>
          </section>
        )}
        {/* end Application Status sections */}
      </SideNavigation>
      <div className="w-full my-5">
        {/* header */}
        <div className="flex flex-row items-center gap-2">
          <GoChecklist className="text-blue-700 w-24 h-24" />
          <div className="flex justify-between w-full">
            <div className="flex flex-col">
              <h1 className="text-4xl font-semibold">
                Applications Re-Evaluate
              </h1>

              <div className="flex flex-row items-center md:gap-3 gap-2">
                <div className="flex flex-row items-center mt-1 gap-1">
                  <span className="text-gray-600 text-lg">level:</span>
                  <span className="text-gray-600 text-lg font-bold">
                    Country
                  </span>
                  <MdKeyboardArrowRight className="w-5 h-5" />
                  <span className="text-blue-600 text-lg font-bold">
                    Room Alpha
                  </span>
                </div>
              </div>
            </div>
            {/* <div className="text-right flex flex-col">
              <span className="text-gray-700">Nyarugenge District</span>
              <span className="text-gray-700">Center of Nyakabanda</span>
            </div> */}
          </div>
        </div>
        {/* end header */}

        {/* <div className="shadow bg-white my-3"> */}
        <div className="md:w-98 bg-white mx-auto md:p-2">
          <Table
            columns={tableCols}
            rows={tableData}
            pagination={true}
            search={true}
            thColor="gray"
          />
        </div>
        {/* </div> */}

        {/* modal for creating new categories or sub category */}
        {modalState && application && (
          <Modal
            backDrop={true}
            theme={Themes.default}
            close={(): void => {
              !loadingRequest && setModalState(false);
              !loadingRequest && setApplication(null);
            }}
            displayClose={true}
            backDropClose={false}
            title={
              <div className="text-gray-600 md:ml-4">
                <span className="text-3xl font-bold text-blue-600">
                  {application.fname + " " + application.lname}'s application
                  re-evaluating
                </span>
                <div className="felx items-center">
                  <span>Applied to:</span>
                  <span className="ml-2">Teaching mathematics</span>
                </div>
              </div>
            }
            widthSizeClass={ModalSize.extraLarge}
            padding={{ title: true, body: false, footer: false }}
          >
            <UserDocuments
              thisApplication={application}
              // candidateCheckData={candidateCheckData}
              // filterByCategoryId={filterByCategoryId}
              // filterBySubCategoryId={filterBySubCategoryId}
              // setDoc={setDoc}
              // setDocTitle={setDocTitle}
              // setDocSubTitle={setDocSubTitle}
              // setCandidateCheckData={setCandidateCheckData}
              // setDocIndex={setDocIndex}
              setThisApplication={setApplication}
            />
          </Modal>
        )}
        {/* end modal for creating new categories or sub category */}
      </div>
    </Fragment>
  );
};

const mapStateToProps = ({
  applicationToAssess,
  positionRequests,
  document,
}: StoreState): {
  applicationToAssess: ApplicationAssessmentInterface[];
  positions: PositionInterface[];
  documentCategories: DocumentReducerInterface;
} => {
  return {
    applicationToAssess,
    positions: positionRequests,
    documentCategories: document,
  };
};

export default connect(mapStateToProps, {
  fetchPositions,
  getAllApplicationToAssess,
  getDocumentCategory,
  getDocumentSubCategory,
})(Index);
