import axios from "axios";
import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import {
  DocumentCategoryInterface,
  DocumentAttributesInterface,
} from "../../shared/interfaces";
import { API } from "../../utils/api";
import {
  createDocumentCategory,
  updateDocumentCategory,
} from "../../actions/document";
import { MdClose, MdErrorOutline } from "react-icons/md";

interface AppProps {
  thisCategory: DocumentCategoryInterface | null;
  setCategoryId: React.Dispatch<React.SetStateAction<string>>;
  categoryId: string;
  setCategoryTitle: React.Dispatch<React.SetStateAction<string>>;
  categoryTitle: string;
  setCategoryDescription: React.Dispatch<React.SetStateAction<string>>;
  categoryDescription: string;
  setActionType: React.Dispatch<
    React.SetStateAction<
      | ""
      | "CREATE_SUB"
      | "CREATE_CAT"
      | "UPDATE_SUB"
      | "UPDATE_CAT"
      | "CREATE_FIELD"
      | "UPDATE_FIELD"
    >
  >;
  actionType: string;
  setFeedBack: React.Dispatch<
    React.SetStateAction<
      | {
          type: "success" | "danger" | "warning" | "";
          message: string;
        }
      | undefined
    >
  >;
  feedBack:
    | {
        type: "success" | "danger" | "warning" | "";
        message: string;
      }
    | undefined;
  createDocumentCategory: Function;
  updateDocumentCategory: Function;
  categoryAttributes: DocumentAttributesInterface[];
  setCategoryAttributes: React.Dispatch<
    React.SetStateAction<DocumentAttributesInterface[]>
  >;
}
const CreateDocument: React.FC<AppProps> = ({
  thisCategory,
  categoryId,
  categoryTitle,
  categoryDescription,
  categoryAttributes,
  actionType,
  feedBack,
  createDocumentCategory,
  updateDocumentCategory,
  setCategoryId,
  setCategoryTitle,
  setCategoryDescription,
  setCategoryAttributes,
  setActionType,
  setFeedBack,
}) => {
  useEffect(() => {
    if (thisCategory && actionType === "UPDATE_CAT") {
      setCategoryId(thisCategory.user_detail_category_id);
      setCategoryTitle(thisCategory.title);
      setCategoryDescription(thisCategory.description);
      setCategoryAttributes(thisCategory.fields);
    }
  });

  const [loading, setLoadind] = useState<boolean>(false);

  const updateCategoryValue = (
    value: DocumentAttributesInterface,
    isAvailable: boolean
  ) => {
    // Destructure current state array
    const arrayvalue: DocumentAttributesInterface[] = [...categoryAttributes];
    if (isAvailable) {
      var index = arrayvalue.indexOf(value);
      if (index !== -1) {
        arrayvalue.splice(index, 1);
      }
    } else {
      arrayvalue.push(value);
    }
    setCategoryAttributes(arrayvalue);
  };

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setFeedBack({ type: "", message: "" });
    if (!categoryAttributes.length) {
      setFeedBack({
        type: "warning",
        message: "You must select at least one field",
      });
      return;
    }
    setLoadind(true);
    try {
      if (actionType === "CREATE_CAT") {
        const res = await axios.post<DocumentCategoryInterface>(
          `${API.AUTH}/detailcategory`,
          {
            title: categoryTitle,
            description: categoryDescription,
            fields: categoryAttributes,
          }
        );
        createDocumentCategory(res.data);
        setFeedBack({
          type: "success",
          message: `Category has been created!`,
        });
        setLoadind(false);
        setCategoryTitle("");
        setCategoryDescription("");
        setCategoryAttributes([]);
      } else {
        const formData: DocumentCategoryInterface = {
          user_detail_category_id: categoryId,
          title: categoryTitle,
          description: categoryDescription,
          fields: categoryAttributes,
        };
        const res = await axios.patch<DocumentCategoryInterface>(
          `${API.AUTH}/detailcategory`,
          formData
        );
        if (res.data) {
          updateDocumentCategory(formData);
          setFeedBack({
            type: "success",
            message: `Category has been updated!`,
          });
          setCategoryId("");
          setCategoryTitle("");
          setCategoryDescription("");
          setCategoryAttributes([]);
          setActionType("");
          setLoadind(false);
        }
      }
    } catch (error) {
      console.log({ doc_category_error: error });
      setLoadind(false);
    }
  };

  return (
    <form onSubmit={(e) => handleSubmit(e)}>
      {feedBack && feedBack.type !== "" && (
        <div
          className={`alert-${feedBack.type} flex flex-row items-center relative`}
        >
          <span className="absolute top-0 bottom-0 right-0 px-4 py-3">
            <MdClose
              className="h-5 w-5 text-gray-700 hover:font-bold"
              title="Close"
              onClick={() => setFeedBack({ type: "", message: "" })}
            />
          </span>
          <MdErrorOutline className="h-10 w-10" />
          <div className="alert-content ml-4">
            <div className="alert-title font-semibold text-lg">
              {feedBack.type === "success" ? "Success" : "Error"}
            </div>
            <div className="alert-description text-sm">{feedBack.message}</div>
          </div>
        </div>
      )}
      <div className="divide-y divide-gray-200">
        <div className="py-8 text-base leading-6 space-y-4 text-gray-700 sm:text-lg sm:leading-7">
          <div className="flex flex-col">
            <label>Title</label>
            <input
              type="text"
              className="text-left input-white w-20 inline-block border-gray-500 border-2"
              placeholder="Sub Category Title"
              value={categoryTitle}
              onChange={(e) => setCategoryTitle(e.target.value)}
              required
              disabled={loading}
            />
          </div>
          <div className="flex flex-col">
            <label>Description</label>
            <textarea
              className="text-left input-white w-20 inline-block border-gray-500 border-2"
              placeholder="Category Description"
              style={{ resize: "none" }}
              value={categoryDescription}
              onChange={(e) => setCategoryDescription(e.target.value)}
              rows={2}
              required
              disabled={loading}
            ></textarea>
          </div>
          <div className="flex flex-col">
            <label>Attributes</label>
            <span className="text-gray-500 text-sm">
              Required Attributes for this document
            </span>
            <div className="grid grid-flow-row grid-cols-3 grid-rows- gap-4">
              <label className="inline-flex items-center mt-3">
                <input
                  type="checkbox"
                  className="form-checkbox h-5 w-5 text-gray-600"
                  onChange={() =>
                    updateCategoryValue(
                      DocumentAttributesInterface.Title,
                      categoryAttributes.includes(
                        DocumentAttributesInterface.Title
                      )
                    )
                  }
                  checked={
                    categoryAttributes &&
                    categoryAttributes.includes(
                      DocumentAttributesInterface.Title
                    )
                  }
                  disabled={loading}
                />
                <span className="ml-2 text-gray-700">Title</span>
              </label>
              <label className="inline-flex items-center mt-3">
                <input
                  type="checkbox"
                  className="form-checkbox h-5 w-5 text-gray-600"
                  onChange={() =>
                    updateCategoryValue(
                      DocumentAttributesInterface.Description,
                      categoryAttributes.includes(
                        DocumentAttributesInterface.Description
                      )
                    )
                  }
                  checked={
                    categoryAttributes &&
                    categoryAttributes.includes(
                      DocumentAttributesInterface.Description
                    )
                  }
                  disabled={loading}
                />
                <span className="ml-2 text-gray-700">Description</span>
              </label>
              <label className="inline-flex items-center mt-3">
                <input
                  type="checkbox"
                  className="form-checkbox h-5 w-5 text-gray-600"
                  onChange={() =>
                    updateCategoryValue(
                      DocumentAttributesInterface.Files,
                      categoryAttributes.includes(
                        DocumentAttributesInterface.Files
                      )
                    )
                  }
                  checked={
                    categoryAttributes &&
                    categoryAttributes.includes(
                      DocumentAttributesInterface.Files
                    )
                  }
                  disabled={loading}
                />
                <span className="ml-2 text-gray-700">Files</span>
              </label>
              <label className="inline-flex items-center mt-3">
                <input
                  type="checkbox"
                  className="form-checkbox h-5 w-5 text-gray-600"
                  onChange={() =>
                    updateCategoryValue(
                      DocumentAttributesInterface.email,
                      categoryAttributes.includes(
                        DocumentAttributesInterface.email
                      )
                    )
                  }
                  checked={
                    categoryAttributes &&
                    categoryAttributes.includes(
                      DocumentAttributesInterface.email
                    )
                  }
                  disabled={loading}
                />
                <span className="ml-2 text-gray-700">Email</span>
              </label>
              <label className="inline-flex items-center mt-3">
                <input
                  type="checkbox"
                  className="form-checkbox h-5 w-5 text-gray-600"
                  onChange={() =>
                    updateCategoryValue(
                      DocumentAttributesInterface.names,
                      categoryAttributes.includes(
                        DocumentAttributesInterface.names
                      )
                    )
                  }
                  checked={
                    categoryAttributes &&
                    categoryAttributes.includes(
                      DocumentAttributesInterface.names
                    )
                  }
                />
                <span className="ml-2 text-gray-700">Names</span>
              </label>
              <label className="inline-flex items-center mt-3">
                <input
                  type="checkbox"
                  className="form-checkbox h-5 w-5 text-gray-600"
                  onChange={() =>
                    updateCategoryValue(
                      DocumentAttributesInterface.phone_number,
                      categoryAttributes.includes(
                        DocumentAttributesInterface.phone_number
                      )
                    )
                  }
                  checked={
                    categoryAttributes &&
                    categoryAttributes.includes(
                      DocumentAttributesInterface.phone_number
                    )
                  }
                  disabled={loading}
                />
                <span className="ml-2 text-gray-700">Phone</span>
              </label>
              <label className="inline-flex items-center mt-3">
                <input
                  type="checkbox"
                  className="form-checkbox h-5 w-5 text-gray-600"
                  onChange={() =>
                    updateCategoryValue(
                      DocumentAttributesInterface.position,
                      categoryAttributes.includes(
                        DocumentAttributesInterface.position
                      )
                    )
                  }
                  checked={
                    categoryAttributes &&
                    categoryAttributes.includes(
                      DocumentAttributesInterface.position
                    )
                  }
                  disabled={loading}
                />
                <span className="ml-2 text-gray-700">Position</span>
              </label>
              <label className="inline-flex items-center mt-3">
                <input
                  type="checkbox"
                  className="form-checkbox h-5 w-5 text-gray-600"
                  onChange={() =>
                    updateCategoryValue(
                      DocumentAttributesInterface.relation,
                      categoryAttributes.includes(
                        DocumentAttributesInterface.relation
                      )
                    )
                  }
                  checked={
                    categoryAttributes &&
                    categoryAttributes.includes(
                      DocumentAttributesInterface.relation
                    )
                  }
                  disabled={loading}
                />
                <span className="ml-2 text-gray-700">Relation</span>
              </label>
              <label className="inline-flex items-center mt-3">
                <input
                  type="checkbox"
                  className="form-checkbox h-5 w-5 text-gray-600"
                  onChange={() =>
                    updateCategoryValue(
                      DocumentAttributesInterface.src,
                      categoryAttributes.includes(
                        DocumentAttributesInterface.src
                      )
                    )
                  }
                  checked={
                    categoryAttributes &&
                    categoryAttributes.includes(DocumentAttributesInterface.src)
                  }
                  disabled={loading}
                />
                <span className="ml-2 text-gray-700">SRC</span>
              </label>
              <label className="inline-flex items-center mt-3">
                <input
                  type="checkbox"
                  className="form-checkbox h-5 w-5 text-gray-600"
                  onChange={() =>
                    updateCategoryValue(
                      DocumentAttributesInterface.year,
                      categoryAttributes.includes(
                        DocumentAttributesInterface.year
                      )
                    )
                  }
                  checked={
                    categoryAttributes &&
                    categoryAttributes.includes(
                      DocumentAttributesInterface.year
                    )
                  }
                  disabled={loading}
                />
                <span className="ml-2 text-gray-700">Year</span>
              </label>
              <label className="inline-flex items-center mt-3">
                <input
                  type="checkbox"
                  className="form-checkbox h-5 w-5 text-gray-600"
                  onChange={() =>
                    updateCategoryValue(
                      DocumentAttributesInterface.company,
                      categoryAttributes.includes(
                        DocumentAttributesInterface.company
                      )
                    )
                  }
                  checked={
                    categoryAttributes &&
                    categoryAttributes.includes(
                      DocumentAttributesInterface.company
                    )
                  }
                  disabled={loading}
                />
                <span className="ml-2 text-gray-700">Company</span>
              </label>
            </div>
          </div>
        </div>
        <div className="pt-4 flex justify-between">
          <button
            onClick={() => {
              setActionType("");
              setCategoryTitle("");
              setCategoryDescription("");
              setCategoryAttributes([]);
            }}
            className="btn bg-gray-400 text-white hover:bg-gray-600"
            type="button"
            disabled={loading}
          >
            Exit
          </button>
          <button
            className="btn bg-primary text-white hover:bg-blue-600"
            type="submit"
            disabled={loading}
          >
            {loading
              ? "Loading..."
              : actionType === "CREATE_CAT"
              ? "Create"
              : "Save Change"}
          </button>
        </div>
      </div>
    </form>
  );
};

export default connect(null, {
  createDocumentCategory,
  updateDocumentCategory,
})(CreateDocument);
